"use client";

import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useAdminStore } from "@/store/admin-store";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Plus, Mail, Phone, User, FileText } from "lucide-react";
import {
	DropdownMenu,
	DropdownMenuTrigger,
	DropdownMenuContent,
	DropdownMenuItem,
} from "@/components/ui/dropdown-menu";

const ClientesPage = () => {
	const router = useRouter();
	const { isAuthenticated, clients, fetchClients, addClient, isLoading } = useAdminStore();
	const [showAddForm, setShowAddForm] = useState(false);
	const [formData, setFormData] = useState({
		name: "",
		email: "",
		cpf: "",
		phone: "",
	});

	useEffect(() => {
		if (!isAuthenticated) {
			router.push("/admin/login");
		} else {
			fetchClients();
		}
	}, [isAuthenticated]);

	const handleSubmit = async (e) => {
		e.preventDefault();
		await addClient(formData);
		setFormData({ name: "", email: "", cpf: "", phone: "" });
		setShowAddForm(false);
	};

	return (
		<div className="flex flex-col gap-6">
			<div className="flex items-center justify-between border-b pb-4">
				<div>
					<h1 className="text-2xl font-bold">Clientes</h1>
					<p className="text-sm text-muted-foreground">
						Gerencie todos os clientes cadastrados
					</p>
				</div>
				<Button onClick={() => setShowAddForm(!showAddForm)}>
					<Plus className="h-4 w-4" />
					Novo Cliente
				</Button>
			</div>

			{showAddForm && (
				<Card>
					<CardHeader>
						<CardTitle>Adicionar Novo Cliente</CardTitle>
					</CardHeader>
					<CardContent>
						<form onSubmit={handleSubmit} className="space-y-4">
							<div className="grid grid-cols-2 gap-4">
								<div className="space-y-2">
									<Label htmlFor="name">Nome Completo</Label>
									<Input
										id="name"
										value={formData.name}
										onChange={(e) => setFormData({ ...formData, name: e.target.value })}
										required
									/>
								</div>
								<div className="space-y-2">
									<Label htmlFor="email">Email</Label>
									<Input
										id="email"
										type="email"
										value={formData.email}
										onChange={(e) => setFormData({ ...formData, email: e.target.value })}
										required
									/>
								</div>
								<div className="space-y-2">
									<Label htmlFor="cpf">CPF</Label>
									<Input
										id="cpf"
										value={formData.cpf}
										onChange={(e) => setFormData({ ...formData, cpf: e.target.value })}
										placeholder="000.000.000-00"
										required
									/>
								</div>
								<div className="space-y-2">
									<Label htmlFor="phone">Telefone</Label>
									<Input
										id="phone"
										value={formData.phone}
										onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
										placeholder="(00) 00000-0000"
										required
									/>
								</div>
							</div>
							<div className="flex gap-2 justify-end">
								<Button type="button" variant="outline" onClick={() => setShowAddForm(false)}>
									Cancelar
								</Button>
								<Button type="submit" disabled={isLoading}>
									Salvar Cliente
								</Button>
							</div>
						</form>
					</CardContent>
				</Card>
			)}

			<div className="grid gap-4">
				{clients.map((client) => (
					<Card key={client.id} className="hover:shadow-md transition-shadow">
						<CardHeader>
							<div className="flex items-start justify-between">
								<div className="flex-1">
									<CardTitle className="text-lg mb-3">{client.name}</CardTitle>
									<div className="grid grid-cols-2 gap-3 text-sm">
										<div className="flex items-center gap-2 text-muted-foreground">
											<Mail className="h-4 w-4" />
											<span>{client.email}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<Phone className="h-4 w-4" />
											<span>{client.phone}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<User className="h-4 w-4" />
											<span>{client.cpf}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<FileText className="h-4 w-4" />
											<span>{client.processCount} processos</span>
										</div>
									</div>
								</div>
								<div className="flex gap-2">
									<Button variant="outline" size="sm">
										Ver Processos
									</Button>
								</div>
							</div>
						</CardHeader>
					</Card>
				))}
			</div>
		</div>
	);
};

export default ClientesPage;

